html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-default);
  font-size: 1.125rem;
}

h1 {
  font-size: 4.375rem;
  line-height: 5.25rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 768px) {
  h1 {
    font-size: 3.375rem;
    line-height: 4rem;
  }
}
h2 {
  font-size: 3rem;
  line-height: 3.5rem;
  letter-spacing: 0.36px;
  font-weight: 400;
}

@media (max-width: 768px) {
  h2 {
    font-size: 2.5rem;
    line-height: 3rem;
  }
}
h3 {
  font-size: 2.5rem;
  line-height: 3rem;
  letter-spacing: 0.36px;
}

h4 {
  font-size: 1.875rem;
  line-height: 2rem;
  font-weight: 400;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --white: #fff;
  --grey1: #f7f7f7;
  --grey2: #f1f1f1;
  --grey3: #d4d4d4;
  --grey4: #b8b8b8;
  --grey4: #525c60;
  --primary: #7eb693;
  --secondary: #ffa858;
  --body: #274c5b;
}

:root {
  --font-default: "Roboto", sans-serif;
  --font-display: "Yellowtail", cursive;
}

.header {
  font-size: 5rem;
}